import React, { useEffect, useRef, forwardRef, useImperativeHandle } from 'react';

import ButtonIconOnly from '../natura11y/button/ButtonIconOnly';

import { getFocusableElements, focusTrap } from '../../utilities/focus';

const ElementModal = forwardRef((props, ref) => {
    const {
        scrollAll = false,
        closeOutside = false,
        title = 'Modal Title',
        isOpen = false,
        handleModalClose = null,
        children = <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
    } = props;

    const modalContainer = useRef(null);
    const modalContent = useRef(null);

    useImperativeHandle(ref, () => modalContainer.current);

    const handleKeyDown = (event) => {
        if (!modalContent.current) return;

        const focusableElements = getFocusableElements(modalContent.current);
        if (focusableElements.length === 0) return;

        const firstElement = focusableElements[0];
        const lastElement = focusableElements[focusableElements.length - 1];

        switch (event.code) {
            case 'Tab':
                if (document.activeElement === lastElement && !event.shiftKey) {
                    event.preventDefault();
                    firstElement.focus();
                } else if (document.activeElement === firstElement && event.shiftKey) {
                    event.preventDefault();
                    lastElement.focus();
                }
                break;

            case 'Escape':
                handleModalClose();
                break;

            default:
                break;
        }
    };

    useEffect(() => {
        if (isOpen) {
            document.addEventListener('keydown', handleKeyDown);
            focusTrap(modalContent.current);
        } else {
            document.removeEventListener('keydown', handleKeyDown);
        }

        return () => {
            document.removeEventListener('keydown', handleKeyDown);
        };
    }, [isOpen]);

    const handleCloseOutside = (event) => {
        if (closeOutside && modalContent.current && !modalContent.current.contains(event.target)) {
            handleModalClose();
        }
    };

    return (
        <div
            className={`modal ${scrollAll ? 'modal--scroll-all' : ''} padding-4`}
            ref={modalContainer}
            role='dialog'
            aria-hidden={!isOpen}
            tabIndex={isOpen ? 0 : -1}
            onClick={handleCloseOutside}
        >
            <div
                className='modal__content narrow box-shadow-3'
                aria-labelledby='modal-element-title'
                ref={modalContent}
            >
                <header className='modal__content__head'>
                    <h2 className='h6' id='modal-element-title'>
                        {title}
                    </h2>
                    <ButtonIconOnly
						buttonType='button'
						iconHandle='close'
						clickHandler={handleModalClose}
					/>
                </header>

                <div className='modal__content__container' style={{ paddingTop: 'initial' }}>
                    {children}
                </div>

                <footer className='modal__content__foot border-top text-color-link font-size-md'>
                    <a
                        className='button button--has-icon'
                        href='https://en.wikipedia.org/wiki/Hydrogen'
                        target='_blank'
                        rel='noopener noreferrer'
                    >
                        <span className='button__text'>More on Wikipedia</span>
                        <span className='icon icon-open-new'></span>
                    </a>
                </footer>
            </div>
        </div>
    );
});

export default ElementModal;