import React, { useRef, useState, useEffect } from 'react';

import { graphql } from 'gatsby';

import { handleOverlayOpen, handleOverlayClose } from '../utilities/overlay';

import Header from '../components/ui/Header';
import ElementModal from '../components/ui/ElementModal';

const PeriodicTable = ({ data }) => {
    const elements = data.allDataJson.edges[0].node.elements;

    const [scrollable, setScrollable] = useState(false);
    const [isScrolling, setIsScrolling] = useState(false);
    const [isOpen, setIsOpen] = useState(false);
    const [selectedElement, setSelectedElement] = useState(null);
    const [view, setView] = useState('table'); // State to manage current view

    const scrollElement = useRef(null);
    const scrollTarget = useRef(null);
    const lastFocused = useRef(null);
    const modalContainer = useRef(null);

    const handleModalOpen = (element) => (e) => {
        setSelectedElement(element);
        setIsOpen(true);
        handleOverlayOpen(modalContainer.current);
        lastFocused.current = e.target;
    };

    const handleModalClose = () => {
        setIsOpen(false);
        handleOverlayClose(modalContainer.current);
        lastFocused.current.focus();
    };

    const checkWidths = () => {
        const maxWidth = scrollElement.current ? scrollElement.current.offsetWidth : 0;
        const scrollWidth = scrollTarget.current ? scrollTarget.current.scrollWidth : 0;
        setScrollable(scrollWidth > maxWidth);
    };

    useEffect(() => {
        checkWidths();
        window.addEventListener('resize', checkWidths);
        return () => {
            window.removeEventListener('resize', checkWidths);
        };
    }, []);

    const scrollHandler = () => {
        const scrollPosition = scrollTarget.current ? scrollTarget.current.scrollLeft : 0;
        setIsScrolling(scrollPosition > 1);
    };

    const elementTableButtons = elements.map((element) => (
        <button
            key={element.symbol}
            className='periodic-table__element'
            style={{ gridArea: `${element.symbol}` }}
            title={element.name}
            data-group={element.chemicalGroup.toLowerCase().replace(/\s+/g, '-')}
            data-modal-open='default'
            onClick={handleModalOpen(element)}
        >
            <p className='atomic-number'>{element.atomicNumber}</p>
            <p className='symbol'>{element.symbol}</p>
            <p className='atomic-mass'>{element.atomicMass}</p>
            <p className='chemical-group'>{element.chemicalGroup}</p>
        </button>
    ));

    const elementList = elements.map((element) => (
        <div key={element.symbol} className='element-list-item'>
            <h3>{element.name} ({element.symbol})</h3>
            <p>Atomic Number: {element.atomicNumber}</p>
            <p>Atomic Mass: {element.atomicMass}</p>
            <p>Group: {element.chemicalGroup}</p>
            <p>Standard State: {element.standardState}</p>
            <p>Electron Configuration: {element.electronConfiguration}</p>
            <p>Oxidation States: {element.oxidationStates}</p>
            <p>Atomic Radius: {element.atomicRadius}</p>
            <p>Electron Affinity: {element.electronAffinity}</p>
            <p>Ionization Energy: {element.ionizationEnergy}</p>
            <p>Melting Point: {element.meltingPoint}</p>
            <p>Boiling Point: {element.boilingPoint}</p>
            <p>Density: {element.density}</p>
            <p>Year Discovered: {element.yearDiscovered}</p>
            <button onClick={handleModalOpen(element)}>More Info</button>
        </div>
    ));

    return (
        <div>
            <Header />

            <main className='padding-y-5'>

            <div className="container wide">
            
                <div className='tabs-nav tabs-nav--horizontal'>
                <li>
                    <button onClick={() => setView('table')} className={view === 'table' ? 'active' : ''}>Table View</button>
                </li>
                <li>
                    <button onClick={() => setView('list')} className={view === 'list' ? 'active' : ''}>List View</button>
                </li>
                </div>
            </div>


                {view === 'table' && (
                    <div
                        className='container table-scroll'
                        data-scroll={scrollable}
                        ref={scrollElement}
                    >
                        <small className='table-scroll__help'>
                            Scroll side to side to see the whole table
                        </small>

                        <div
                            className='table-scroll__container'
                            ref={scrollTarget}
                            data-scrolling={isScrolling}
                            onScroll={scrollHandler}
                        >
                            <div className='periodic-table table' id='periodic-table-of-elements'>
                                <div className='periodic-table__key-element'>
                                    <div className='periodic-table__element' data-group='halogen'>
                                        <p className='atomic-number'>
                                            17
                                            <span className='periodic-table__key__item'>Atomic Number</span>
                                        </p>
                                        <p className='symbol'>
                                            Cl
                                            <span className='periodic-table__key__item'>Symbol</span>
                                        </p>
                                        <p className='atomic-mass'>
                                            35.45
                                            <span className='periodic-table__key__item'>Atomic Mass</span>
                                        </p>
                                    </div>
                                </div>

                                <ul className='periodic-table__key-color'>
                                    <li data-color='alkali-metals'>Alkali Metals</li>
                                    <li data-color='alkaline-earth-metals'>Alkaline Earth Metals</li>
                                    <li data-color='transition-metals'>Transition Metals</li>
                                    <li data-color='post-transition-metals'>Post-Transition Metals</li>
                                    <li data-color='metalloids'>Metalloids</li>
                                    <li data-color='nonmetals'>Reactive Nonmetals</li>
                                    <li data-color='halogens'>Halogens</li>
                                    <li data-color='noble-gases'>Noble Gases</li>
                                    <li data-color='lanthanides'>Lanthanides</li>
                                    <li data-color='actinides'>Actinides</li>
                                </ul>

                                <div className='periodic-table__connectors' aria-hidden='true'>
                                    <div className='lanthanides-connector'></div>
                                    <div className='actinides-connector'></div>
                                </div>

                                <div className='separator' aria-hidden='true'></div>

                                {elementTableButtons}
                            </div>
                        </div>
                    </div>
                )}

                {view === 'list' && (
                    <div className='container wide'>
                        {elementList}
                    </div>
                )}
            </main>

            {selectedElement && (
                <ElementModal
                    isOpen={isOpen}
                    handleModalClose={handleModalClose}
                    closeOutside={true}
                    scrollAll={true}
                    title='Element Properties'
                    ref={modalContainer}
                >
                    <div className='modal__content__body' data-group={selectedElement.chemicalGroup.toLowerCase().replace(/\s+/g, '-')}>
                        <div className='flex-column text-align-center padding-y-3'>
                            <span className='font-size-md'>{selectedElement.atomicNumber}</span>
                            <span className='h1 margin-bottom-0'>{selectedElement.symbol}</span>
                            <span className='h4 name'>{selectedElement.name}</span>
                            <span className='font-size-md'>{selectedElement.chemicalGroup}</span>
                        </div>
                    </div>

                    <div className='modal__content__body'>
                        <p className='font-size-md'>{selectedElement.description}</p>
                        <table className='table table--edge'>
                            <tbody>
                                <tr>
                                    <td><strong>Atomic Mass</strong></td>
                                    <td>{selectedElement.atomicMass} u</td>
                                </tr>
                                <tr>
                                    <td><strong>Standard State</strong></td>
                                    <td>{selectedElement.standardState}</td>
                                </tr>
                                <tr>
                                    <td><strong>Electron Configuration</strong></td>
                                    <td>{selectedElement.electronConfiguration}</td>
                                </tr>
                                <tr>
                                    <td><strong>Oxidation States</strong></td>
                                    <td>{selectedElement.oxidationStates}</td>
                                </tr>
                                <tr>
                                    <td><strong>Atomic Radius (van der Waals)</strong></td>
                                    <td>{selectedElement.atomicRadius} pm</td>
                                </tr>
                                <tr>
                                    <td><strong>Ionization Energy</strong></td>
                                    <td>{selectedElement.ionizationEnergy} kJ/mol</td>
                                </tr>
                                <tr>
                                    <td><strong>Electron Affinity</strong></td>
                                    <td>{selectedElement.electronAffinity} kJ/mol</td>
                                </tr>
                                <tr>
                                    <td><strong>Melting Point</strong></td>
                                    <td>{selectedElement.meltingPoint} K</td>
                                </tr>
                                <tr>
                                    <td><strong>Boiling Point</strong></td>
                                    <td>{selectedElement.boilingPoint} K</td>
                                </tr>
                                <tr>
                                    <td><strong>Density (standard temp and pressure)</strong></td>
                                    <td>{selectedElement.density} g/cm3</td>
                                </tr>
                                <tr>
                                    <td><strong>Year Discovered</strong></td>
                                    <td>{selectedElement.yearDiscovered}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </ElementModal>
            )}
        </div>
    );
};

export const query = graphql`
    query {
        allDataJson {
            edges {
                node {
                    elements {
                        symbol
                        name
                        description
                        atomicMass
                        atomicNumber
                        chemicalGroup
                        standardState
                        electronConfiguration
                        oxidationStates
                        atomicRadius
                        electronAffinity
                        ionizationEnergy
                        meltingPoint
                        boilingPoint
                        density
                        yearDiscovered
                    }
                }
            }
        }
    }
`;

export default PeriodicTable;
